//Font Family
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
//Font Family

//Variables
$main-color: #9C9EFE;
$main-text-color: #5800FF;
$default-transition-value: 400ms;
$default-transition-value-fast: 200ms;
//Variables

//Reset CSS
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
    list-style: none;
    color: #fff;
}
//Reset CSS

.active{
    @media (max-width: 900px){
    width: 100%;
    background: rgba(0,0,0,0.1);
    border-radius: 5px;
    text-align: center;
    }
    li{
        &:before{
            width: 100%!important;
            @media (max-width: 900px){
                width: 0;
            }
        }
        //color: $main-text-color;
    }
}

::webkit-scrollbar{
    width: 0px;
}

body{
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(to right, #80558C , #5800FF);
    position: relative;
    top: 0;
    margin: 0px auto;
    padding: 10px;
    .box{
        max-width: 1200px;
        margin: 0px auto;
        background: rgba(0,0,0,0.1);
        min-height: 97vh;
        position: relative;
        border-radius: 13px;
        box-shadow: 0px 1px 5px rgba(0,0,0,0.3);
        border: 1px solid rgba(0,0,0,0.05);
        padding: 30px 40px;
        @media (max-width: 768px){
            padding: 20px;
        }
        nav{
            width: 100%;
            height: 60px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            @media (max-width: 900px){
                justify-content: space-between;
            }
            .toggler{
                position: relative;
                width: 7px;
                height: 7px;
                background: #fff;
                border-radius: 30px;
                display: none;
                cursor: pointer;
                margin: 0px 10px;
                @media (max-width: 900px){
                    display: inline;
                }
                &:before, &:after{
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background: #fff;
                    top: 0;
                    left: 0;
                    border-radius: 30px;
                }
                &:before{
                    transform: translateX(-10px);
                }
                &:after{
                    transform: translateX(10px);
                }
            }
            //toggled class for ul
            .openedMenu{
                width: 200px;
                height: 184px;
                opacity: 1;
                visibility: visible;
            }
            //toggled class for ul
            ul{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                position: relative;
                @media (max-width: 900px){
                    position: absolute;
                    flex-direction: column;
                    background: #fff;
                    right: 30px;
                    top: 90px;
                    border-radius: 10px;
                    padding: 10px;
                    z-index: 1000;
                    width: 0px;
                    height: 0px;
                    opacity: 0;
                    visibility: hidden;
                    overflow: hidden;
                    transition: 600ms;
                }
                li{
                    position: relative;
                    margin: 0px 20px;
                    font-size: 13px;
                    font-weight: 400;
                    letter-spacing: 1px;
                    overflow: hidden;
                    @media (max-width: 900px){
                        width: 100%;
                        padding: 10px 0px;
                        color: #000;
                        font-size: 14px;
                        letter-spacing: 0px;
                        font-weight: 500;
                        margin: 0px;
                        //////////////////////////
                        &:before, &:after{display: none;}
                    }
                    &:before{
                        content: '';
                        position: absolute;
                        width: 0%;
                        height: 1.7px;
                        background: #fff;
                        bottom: 0;
                        border-radius: 20px;
                        transition: 300ms;
                        transition-delay: 150ms;
                    }
                    &:hover:before{
                        width: 100%;
                        left: 0;
                    }
                }
            }
            .nav-logo{
                position: relative;
                font-size: 30px;
                font-weight: 700;
                letter-spacing: 2px;
                font-style: italic;
                margin-right: 5%;
                cursor: no-drop;
                @media (max-width: 768px){
                    font-size: 22px;
                }
                p{
                    font-size: 9px;
                    letter-spacing: 1px;
                    font-weight: 500;
                    font-style: italic;
                }
            }
        }
        .home{
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: auto;
            height: 50vh;
            margin-top: 9%;
            text-align: center;
            @media (max-width: 768px){
                height: 80vh;
                margin-top: 17%;
            }
            h1{
                font-size: 60px;
                font-weight: 300;
                font-family: 'Pacifico', cursive;
                @media (max-width: 768px){
                    font-size: 40px;
                }
            }
            h2{
                font-size: 45px;
                font-weight: 100;
                @media (max-width: 768px){
                    font-size: 25px;
                }
            }
            h3{
                font-size: 17px;
                font-weight: 200;
                letter-spacing: 2px;
                @media (max-width: 768px){
                    font-size: 15px;
                }
                b{
                    font-size: 50px;
                    font-weight: 500;
                    font-family: 'Pacifico', cursive;
                    @media (max-width: 768px){
                        font-size: 35px;
                    }
                }
            }
            .home-buttons{
                margin-top: 3%;
                position: relative;
                @media (max-width: 768px){
                    margin-top: 15%;
                }
                span{
                    border: 1px solid #F1EFDC;
                    font-size: 18px;
                    padding: 8px 20px;
                    margin: 10px;
                    border-radius: 40px;
                    //background: rgba(0,0,0,0.1);
                    letter-spacing: 1px;
                    font-weight: 200;
                    transition: $default-transition-value-fast;
                    @media (max-width: 768px){
                        font-size: 14px;
                    }
                    &:hover{
                        box-shadow: 0px 0px 3px #fff;
                        background: #fff;
                        color: $main-color;
                        font-weight: 400;
                    }
                }
            }
        }
        .tech{
            position: relative;
            width: 100%;
            height: auto;
            margin: 0px auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            h1{
                text-align: center;
                font-family: 'Pacifico', cursive;
                letter-spacing: 1px;
                font-size: 25px;
                position: relative;
                margin: 1%;
                font-weight: 200;
                padding: 20px;
            }
            ul{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                gap: 10px;
                li{
                    width: 18%;
                    height: 140px;
                    background: rgba(0,0,0,0.2);
                    margin-top: 40px;
                    border-radius: 10px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                    transition: $default-transition-value;
                    animation: slide $default-transition-value linear;
                    @media (max-width: 900px){
                        width: 25%;
                    }
                    @media (max-width: 768px){
                        width: 31%;
                    }
                    @media (max-width: 500px){
                        width: 46%;
                        height: 120px;
                        margin: 0px;
                    }
                    @mixin keyframes($name) {
                        @keyframes #{$name} {
                        @content;
                        }
                    }
                    @include keyframes(slide){
                        0%{
                            transform: scaleY(0);
                        }
                        100%{
                            transform: scaleY(1);
                        }
                    }
                    &:hover{
                        box-shadow: 0px 0px 7px rgba(0,0,0,0.2);
                        transform: translateY(-10px);
                        background: rgba(0,0,0,0);
                    }
                    img{
                        width: 45%;
                        height: auto;
                        border-radius: 7px;
                        box-shadow: 0px 1px 4px rgba(0,0,0,0.2);
                        padding: 10px;
                        transition: $default-transition-value;
                    }
                }
            }
        }
        .templates{
            position: relative;
            width: 100%;
            height: auto;
            h1{
                text-align: center;
                font-family: 'Pacifico', cursive;
                font-weight: 200;
                font-size: 30px;
                margin: 1% auto;
            }
            ul{
                display: flex;
                justify-content: center;
                margin-top: 20px;
                flex-wrap: wrap;
                gap: 10px;
                li{
                    width: 250px;
                    height: 350px;
                    overflow: hidden;
                    border-radius: 10px;
                    box-shadow: 0px 1px 10px rgba(0,0,0,0.4);
                    border: 2px solid rgba(0,0,0,0.4);
                    font-size: 30px;
                    display: grid;
                    place-items: center;
                    background: rgba(0,0,0,0.4);
                    animation: yscaling $default-transition-value linear;
                    @media (max-width: 550px){
                        width: 80%;
                    }
                    @mixin keyframes($name){
                        @keyframes #{$name}{
                            @content
                        }
                    }
                    @include keyframes(yscaling){
                        0%{
                            transform: scaleX(0);
                        }
                        100%{
                            transform: scaleX(1);
                        }
                    }
                }
            }
        }
        .contact{
            position: relative;
            width: 100%;
            height: auto;
            h1{
                font-family: 'Pacifico', cursive;
                font-size: 25px;
                font-weight: 200;
                text-align: center;
                margin: 20px 0px;
            }
            .contact-box{
                position: relative;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                @media (max-width: 768px){
                    flex-direction: column;
                }
                ul{
                    position: relative;
                    width: 50%;
                    li{
                        width: 300px;
                        height: auto;
                        display: flex;
                        justify-content: flex-start;
                        align-items: center;
                        &:hover{
                            i{
                                box-shadow: 0px 0px 5px rgba(0,0,0,0.5);
                            }
                        }
                        i{
                            font-size: 40px;
                            width: 60px;
                            height: 60px;
                            //border: 1px solid rgba(0,0,0,0.2);
                            text-align: center;
                            line-height: 60px;
                            background: rgba(0,0,0,0.2);
                            transition: $default-transition-value-fast;
                            @media (max-width: 768px){
                                font-size: 30px;
                                width: 50px;
                                height: 50px;
                                line-height: 50px;
                            }
                        }
                        a{
                            font-size: 15px;
                            font-weight: 300;
                            letter-spacing: 1px;
                            padding: 0px 10px;
                        }
                    }
                }
                form{
                    position: relative;
                    width: 50%;
                    display: flex;
                    flex-direction: column;
                    @media (max-width: 768px) {
                        width: 95%;
                    }
                    input, .sug-input{
                        width: 100%;
                        height: 50px;
                        padding: 0px 20px;
                        font-size: 15px;
                        margin: 10px 0px;
                        outline: none;
                        color: #fff;
                        border: none;
                        transition: $default-transition-value-fast;
                        background: rgba(0,0,0,0.2);
                        &::placeholder{
                            color: $main-color;
                        }
                        &:focus{
                            box-shadow: 0px 0px 5px rgba(0,0,0,0.8);
                        }
                    }
                    .sug-input{
                        height: 100px;
                        padding: 10px 20px;
                    }
                    button{
                        position: relative;
                        font-size: 15px;
                        color: $main-color;
                        width: 120px;
                        height: 40px;
                        margin-top: 10px;
                        background: rgba(0,0,0,0.2);
                        border: none;
                        outline: none;
                        &:before{
                            content: "Send";
                            position: absolute;
                            top: 0;
                            left: 0;
                            background: #fff;
                            width: 100%;
                            height: 100%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            clip-path: circle(0px at 50% 100%);
                            transition: $default-transition-value;
                            color: $main-text-color;
                            font-weight: 500;
                        }
                        &:hover:before{
                            clip-path: circle(100%);
                        }
                    }
                }
            }
        }
    }
}

